.photo-card {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    min-width: 400px;
    width: 100%;
    margin-bottom: 20px;
}

.photo-card h2 {
    margin-bottom: 10px;
    color: #333;
}

.photo-card img {
    max-width: 100%;
    height: 100px;
    border-radius: 4px;
    margin: auto;
    display: block;
}

.photo-card p {
    margin: 8px 0;
    color: #555;
}
